import { NextResponse } from 'next/server';

export async function POST(request) {
    const { email, password } = await request.json();

    // Hier solltest du deine Datenbank oder Auth-Provider-Logik einf체gen
    // Beispiel: Benutzer in der Datenbank suchen, Passwort 체berpr체fen usw.

    // Beispielantwort (ersetze mit deiner tats채chlichen Logik)
    if (email === 'test@example.com' && password === 'password') {
        return NextResponse.json({ message: 'Erfolgreich angemeldet' });
    } else {
        return NextResponse.json({ message: 'Falsche E-Mail oder Passwort' }, { status: 401 });
    }
}